@font-face {
  font-family: 'ok1';
  src: url('../assets/fonts/Azonix.otf');
}
@font-face {
  font-family: 'ok';
  src: url('../assets/fonts/garet/Garet-Book.woff2');
}

html{
  overflow-x: hidden !important;
}

body {
  font-family: 'ok';
  overflow-x: hidden;
  margin: 0;
  padding: 0;
  overscroll-behavior: none;
  overflow-y: hidden;
}
.haha{
  font-size: 5rem;
  line-height: 5rem;
  letter-spacing: 0.1rem;
  width: 100%; 
  margin-bottom: -1px;

  color: whitesmoke;
  
}
.lol{
  font-size: 4.2rem;
  
  line-height: 3rem;
  letter-spacing: -0.3rem;
  width: 100%;
  margin-bottom: 10px;

  color: whitesmoke;
}

*{
  box-sizing: border-box;
}

.loader{
  background: black;
  color: white;
  position: absolute;
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-content: center;
  align-items: center;
  z-index: 2;
}
.second p {
  font-size: 12px;
  letter-spacing: -0.3;
}
.progress{
  position: absolute;
  top: 52%;
  height: 1px;
  width: 100%;
  transform: scaleX(0);
  background-color: white;
  transition: all 1s ease;
}

.nav--bar{
  position: absolute;
  display: flex;
  align-content: space-between;
  width: 100%;
  justify-content: space-between;
  margin-top: 30px;
}

.logo{
  margin-left: 40px;
  width: 180px;
  font-size: 20px;
  font-weight: 900;
  margin-top: 0px;
  font-family: 'ok';
  text-decoration: line-through;
}

.section{
  display: flex;
  width: 100vw;
  height: 100vh;
  align-items: center;
}

.section--one--container{
  width: 360px;
  margin-left: 15%;
}

.first h1{
  font-size: 5rem;
  line-height: 5rem;
  letter-spacing: -0.3rem;
  width: 50%;
  margin: 0;;
  margin-top: -50px;
  font-family: 'ok';
}
.second{
  color: white;

}

.button--hero{
  background: #96b2c5;
  color: white;
  border: none;
  font-weight: 600;
  border-radius: 50px;
  padding: 15px 30px;
  margin-top: -4px;
  margin-right: 40px;
  font-size: 0.6rem;
  cursor: pointer;
  transition: all 0.8s ease;
}

.button--customize{
  background: #96b2c5;
  color: black;
  border: none;
  font-weight: 600;
  border-radius: 50px;
  padding: 15px 30px;
  margin-top: 24px;
  margin-right: 40px;
  font-size: 0.6rem;
  cursor: pointer;
  transition: all 0.8s ease;
}
.button--hero:hover{
  background: black;
  color: white;
}

.button--navbar{
  background: black;
  color: white;
  border: none;
  border-radius: 50px;
  padding: 10px 30px;
  margin-right: 40px;
  height: 50px;
  font-size: 0.6rem;
  transition: all 0.8s ease;
}

.button--navbar:hover{
  cursor: pointer;
  background: #96b2c5;
  color: white;
}

.second{
  justify-content: flex-end;
  
}

.section--two--container{
  width: 360px;
  display: flex;
  flex-direction: column;
  align-self: flex-end;
  margin-right: 10%;
  z-index: 1;

}

.section--third--container{
  width: 360px;
  margin-left: 10%;
  z-index: 1;
}

h2{
  font-size: 3rem;
  letter-spacing: -2px;
  line-height: 2.5rem;
}

.custom--bg{
  width: 100%;
 height: 300px;
 
  position: relative;
  top: 0;
  left: 0;
  width: 100%;
  height: auto;
  box-sizing: border-box;
  padding-top: 100px;


}
.custom--bg:after{
  position: absolute;
  width: 100%;
  height: 600px;
  content: '';
  background: inherit;
  z-index: -1;
  top: 0;
  right: 0;
  background: #96b2c5;
  bottom: 0;
  left: 0;
  transform-origin: top left;
  -webkit-transform: skew(20deg);
  -moz-transform: skew(20deg);
  -o-transform: skew(20deg);
  transform: skew(20deg) rotate(21deg);
}

.custom--bg2{
  width: 100%;
  height: 800px;
  background: black;
  position: absolute;
  top: 0;
  left: 0;
}

.last{
  background: black;
  padding: 70px;
  display: flex;
  justify-content: center;
}

.button--footer{
  background: white;
  color: black;
  border: none;
  border-radius: 50px;
  padding: 10px 30px;
  font-size: 0.6rem;
  cursor: pointer;
  transition: all 0.8s ease;
}

.button--footer:hover{
  background: black;
  color: white;
}

#webgi-canvas {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  margin-left: 00px;
  
}

#webgi-canvas-container{
  width: 100vw;
  height: 100vh;
  pointer-events: none;
  position: fixed;
  display: flex;

  justify-content: flex-end;
  flex-direction: column;
  align-items: center;
}

.button--exit{
  width: 90px;
  margin-bottom: 5%;
  z-index: 5;
  position: fixed;
  bottom: 0;
  display: none;
}

.customizer--container{
  position: fixed;
  top: 50%;
  display: none;
  transform: translateY(-50%);
}

.strap--colors{
  list-style: none;
}

.button--colors{
  width: 30px;
  height: 30px;
  border-radius: 50px;
  border: 1px solid white;
  padding: 0;
  margin-bottom: 10px;
  cursor: pointer;
  transition: all 0.8s ease;
}

.button--colors:hover{
  filter: drop-shadow(0 0 0.75rem rgba(28, 17, 52, 0.704));
  transform: scale(1.5);
}

.yellow{
  background-color: #F0D56C;
}

.red{
  background-color: rgb(231, 34, 34);
}

.black{
  background-color: black;
}
.last{
  z-index: 1;
 background-color: black !important;
 background: black;
 position: relative;
}

/* MOBILE ADJUSTMENTS */
@media (max-width: 480px) {


  html{
    overflow-x: hidden !important;
  }
  
 
.first p{
  font-size: .8rem;
}
  .first h1{
    font-size: 2rem;
    line-height: 2rem;
    letter-spacing: -0.1rem;
    width: 50%;
    margin: 0 0 15px 0px;
    margin-top: -50px;
   
   
  }

  .logo{
    font-size: 16px;
    margin-top: 15px;
    text-decoration: line-through;
  }

  .nav--bar{
    margin-top: 0;
    z-index: 1;
    background: #ffffffad;
    padding: 30px 0;
    backdrop-filter: blur(5px);
  }
 
  
  .section--one--container {
    width: 360px;
    margin-left: 6%;
    margin-top: 0%;
    z-index: 1;
  }

  .section--one--container > p {
    width: 70%;
    
  }



  .second{
    align-items: flex-end;
  
  }
  .second p{
    font-size: .7rem;
  }
  
  .section--third--container {
    width: 220px;
    margin-left: 7%;
    z-index: 1;
  }
  .haha{
    font-size: 2.1rem;
    line-height: 1rem;
    letter-spacing: 0.1rem;
    width: 100%; 
    z-index: 2;
    margin-bottom: -1px;
    text-decoration: line-through;
    color: whitesmoke;

    
  }
  .lol{
    font-size: 2.0rem;
    z-index: 2;
    line-height: 2rem;
    letter-spacing: -0.2rem;
    width: 100%;
    margin-bottom: 10px;
   
    color: whitesmoke;
  
  }
  .section--two--container{
    z-index: 2;
    margin-left: 20px;
    align-self: start;
  }
  .custom--bg{

    height: 500px;
    justify-content: center;
    align-items: center;
  }

  .custom--bg:after{
    z-index: 1;
    height: 500px;
  }
  footer{
   z-index: 3;
   background-color: black !important;
   background: black;
  }
  .last{
    z-index: 3;
   background-color: black !important;
   background: black;
   position: relative;
  }
  .third h2 {
    font-size: 1.8rem;
  }
  .first{
    margin-top: 0px;
  }

.third{
  height: 550px;
}
.third p{
  font-size: 11px;
  text-decoration: none !important;
  list-style: none;
}
}
